import { NavLink, Outlet } from "react-router-dom";
import React from 'react';
import { Layout, Menu } from 'antd';
import './LayoutDefault.scss';
import api from "../../utils/request";

const { Header, Footer, Content } = Layout;

function LayoutDefault() {
    return (
        <Layout className="layout-default">
            <Header className="custom-header">
                <div className="header-content">
                    <div className="logo">
                        <img src="./h9.png" alt="Logo" />
                    </div>
                </div>
            </Header>

            <Header className="navbar-header">
                <Menu theme="dark" mode="horizontal" selectable={false} className="navbar">
                    <Menu.Item key="home">
                        <NavLink to="/" className={({ isActive }) => (isActive ? 'active' : '')}>
                            Trang ch
                        </NavLink>
                    </Menu.Item>
                    <Menu.Item key="about">
                        <NavLink to="/Gioithieu" className={({ isActive }) => (isActive ? 'active' : '')}>
                            Gii thiu
                        </NavLink>
                    </Menu.Item>
                    <Menu.Item key="manage">
                        <NavLink to="/GiahanGPLX" className={({ isActive }) => (isActive ? 'active' : '')}>
                            Gia hn GPLX
                        </NavLink>
                    </Menu.Item>
                    <Menu.Item key="exchange">
                        <NavLink to="/CaplaiGPLX" className={({ isActive }) => (isActive ? 'active' : '')}>
                            Cp li  GPLX
                        </NavLink>
                    </Menu.Item>
                    <Menu.Item key="login">
                        <NavLink to={api.baseURL} className={({ isActive }) => (isActive ? 'active' : '')}>
                            ng Xut
                        </NavLink>
                    </Menu.Item>
                </Menu>
            </Header>

            <Content className="content">
                <Outlet />
            </Content>

            <Footer className="footer">
                <div className="footer-content">
                    <ul className="footer-menu">
                        <li><a href="/">Trang ch</a></li>
                        <li><a href="/">Gii thiu</a></li>
                        <li><a href="/">Qun l vi phm</a></li>
                        <li><a href="/">i GPLX trc tuyn</a></li>
                    </ul>
                    <div className="footer-info">
                        <p>Trang Thng tin in t giy php li xe</p>
                        <p>a ch:  D20 - Khu  Th Cu Giy mi - Cu Giy - H Ni</p>
                        <p>in thoi: 1900.599.870</p>
                    </div>
                </div>
            </Footer>
        </Layout>
    );
}

export default LayoutDefault;
