import React, { useState } from 'react';
import { Form, Input, Button, DatePicker, Select, Upload, Checkbox, message, Row, Col } from "antd";
import { UploadOutlined } from "@ant-design/icons";
import axios from 'axios';  // Import axios to make the API call
import './GplxReissueForm.scss'
import api from '../../utils/request';

const GplxReissueForm = () => {
  const [isChecked, setIsChecked] = useState(false);
  const { Option } = Select;
  const [form] = Form.useForm()
  const handleCheckboxChange = (e) => {
    setIsChecked(e.target.checked); // Update the state when checkbox is selected or deselected
  };
  const onFinish = (values) => {
    const fileList = values.image && values.image.fileList;  // Access fileList from the object
    console.log("File List:", fileList);
  
    // Ensure fileList is an array and contains a file
    if (Array.isArray(fileList) && fileList.length > 0) {
      const imageFile = fileList[0].originFileObj;  // Get the file object from the first file
      console.log("Uploaded Image:", imageFile);
  
      const formData = new FormData();
      formData.append('image', imageFile);  // Append the image to the FormData
  
      // Append other form values (except image)
      Object.keys(values).forEach((key) => {
        if (key !== 'image' && values[key]) {
          formData.append(key, values[key]);
        }
      });
  
      axios
      .post(api.addRenew, formData)
      .then((response) => {
        console.log("Response from backend:", response.data);
        message.success("n yu cu cp li GPLX  c gi thnh cng!"); // Success notification
        form.resetFields(); // Clear form fields
      })
      .catch((error) => {
        if (error.response) {
          console.error("Backend error:", error.response.data);
          message.error(`Error: ${error.response.data.message || "Unknown error"}`); // Error message from backend
        } else {
          console.error("Connection error:", error.message);
          message.error("Unable to connect to the server."); // Connection error
        }
      });
  } else {
    console.error("No image file uploaded or invalid file list:", fileList);
    message.error("Please upload an image."); // Notify user about missing file
  }
};
  
  
  return (
    <div className="form-container1">
      <h2 className="form-title1">N YU CU CP LI GPLX</h2>
      <Form  form={form}  layout="vertical"  onFinish={onFinish}>
        {/* M GPLX */}
        <Form.Item
          name="MaGPLX"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>M GPLX:</span>}        
          rules={[{ required: true, message: "Vui lng nhp M GPLX!" }]}>
          <Input className="form-item-input" placeholder="Nhp m GPLX" />
        </Form.Item>

        {/* Tn */}
        <Form.Item
          name="Name"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>H v tn:</span>}      
          rules={[{ required: true, message: "Vui lng nhp tn!" }]}>
          <Input className="form-item-input" placeholder="Nhp h v tn" />
        </Form.Item>
        <Form.Item
          name="Lidocaplai"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>L do cp li:</span>}      
           rules={[{ required: true, message: "Vui lng nhp l do cp li!" }]}>
          <Input className="form-item-input" placeholder="Nhp l do cp li" />
        </Form.Item>
          {/* Ngy sinh */}
          <Form.Item
            name="DateOfBirth"
            label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>Ngy sinh:</span>}      
            rules={[{ required: true, message: "Vui lng chn ngy sinh!" }]}>
            <DatePicker style={{ width: "100%" }} />
          </Form.Item>

    {/* Gii tnh */}
       <Form.Item
        name="Gender"
        label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>Gii tnh</span>}
        rules={[{ required: true, message: "Vui lng chn gii tnh!" }]}
      >
        <Select placeholder="Chn gii tnh">
          <Option value="Male">Nam</Option>
          <Option value="Female">N</Option>
        </Select>
      </Form.Item>

        {/* CCCD */}
        <Form.Item
          name="CCCD"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>CCCD:</span>}   
          rules={[{ required: true, message: "Vui lng nhp CCCD!" }]}>
          <Input className="form-item-input" placeholder="Nhp CCCD" />
        </Form.Item>


        {/* S in thoi */}
        <Form.Item
          name="PhoneNumber"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>S in thoi:</span>}   
          rules={[
            { required: true, message: "Vui lng nhp s in thoi!" },
            { pattern: /^[0-9]{10}$/, message: "S in thoi khng hp l!" },
          ]}>
          <Input className="form-item-input" placeholder="Nhp s in thoi" />
        </Form.Item>

        {/* Email */}
        <Form.Item
          name="Email"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>Email:</span>}   
          rules={[
            { required: true, message: "Vui lng nhp email!" },
            { type: "email", message: "Email khng hp l!" },
          ]}>
          <Input className="form-item-input" placeholder="Nhp email" />
        </Form.Item>

        
        {/* a ch */}
        <Form.Item
          name="Address"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>a ch:</span>}   
          rules={[{ required: true, message: "Vui lng nhp a ch!" }]}>
          <Input className="form-item-input" placeholder="Nhp a ch" />
        </Form.Item>

        <Row gutter={16}>

        <Col span={8}>
          <Form.Item
            name="Ngaytrungtuyen"
            label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight: 'bold' }}>Ngy trng tuyn:</span>}
            rules={[{ required: true, message: "Vui lng chn ngy trng tuyn!" }]}
          >
            <DatePicker style={{ width: "100%" }} />
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item
            name="Ngaycap"
            label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight: 'bold' }}>Ngy cp:</span>}
            rules={[{ required: true, message: "Vui lng chn ngy cp!" }]}
          >
            <DatePicker style={{ width: "100%" }} />
          </Form.Item>
        </Col>
        
        <Col span={8}>
          <Form.Item
            name="Ngayhethan"
            label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight: 'bold' }}>Ngy ht hn:</span>}
            rules={[{ required: true, message: "Vui lng chn ngy ht hn!" }]}
          >
            <DatePicker style={{ width: "100%" }} />
          </Form.Item>
        </Col>
      
      </Row>

        {/* Hng GPLX */}
        <Form.Item
          name="HangGPLX"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>Hng GPLX:</span>}   
          rules={[{ required: true, message: "Vui lng chn hng GPLX!" }]}>
          <Select  placeholder="Chn hng GPLX">
            <Select.Option value="B1">B1</Select.Option>
            <Select.Option value="B2">B2</Select.Option>
            <Select.Option value="C">C</Select.Option>
            <Select.Option value="D">D</Select.Option>
            <Select.Option value="E">E</Select.Option>
          </Select>
        </Form.Item>
                
        {/* Quc gia */}
        <Form.Item
                name="Country"
                label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>Quc gia</span>}
                rules={[{ required: true, message: 'Vui lng chn quc gia!' }]}
              >
                <Select placeholder="Chn quc gia">
                  <Option value="Vietnam">Vit Nam</Option>
                  <Option value="USA">M</Option>
                  <Option value="UK">Anh</Option>
                  <Option value="France">Php</Option>
                  <Option value="Germany">c</Option>
                  <Option value="Other">Khc</Option>
                </Select>
              </Form.Item>

        <Form.Item
          name="status"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>Trng thi:</span>}   
          rules={[{ required: true, message: "Vui lng chn trng thi!" }]}>
          <Select placeholder="Chn trng thi">
            <Select.Option value="Ch kim nh">Ch kim nh</Select.Option>
          </Select>
        </Form.Item>

        {/* Gim c */}
        <Form.Item
          name="Giamdoc"
          label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>Gim c:</span>}      
           rules={[{ required: true, message: "Vui lng nhp gim c!" }]}>
          <Input className="form-item-input" placeholder="Nhp tn gim c" />
        </Form.Item>  

       
   {/* Image upload */}
   <Form.Item
        name="image"
        label={<span style={{ fontFamily: 'Times New Roman', fontSize: '17px', fontWeight:'bold'}}>Hnh nh GPLX:</span>}
                rules={[{ required: true, message: "Vui lng cung cp hnh nh!" }]}>
  
        <Upload
          id="image"
          name="image"
          listType="picture"
          maxCount={1}
          beforeUpload={() => false} // Prevent auto-upload
        >
          <Button icon={<UploadOutlined />}>Ti nh ln</Button>
        </Upload>
      </Form.Item>



        <Form.Item>
           <div className="form-actions1" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', textAlign: 'center' }}>
          <Checkbox onChange={handleCheckboxChange}>
            <span style={{ color: 'red', fontWeight: 'bold', fontFamily: 'Times New Roman', fontSize: '18px' }}>
              Ti xin m bo cc thng tin khai bo l chnh xc v xin chu trch nhim v thng tin  khai bo.
            </span>
          </Checkbox>


            {/* Nt Gi yu cu - ch hin th khi checkbox c chn */}
            {isChecked && (
              <Button
                type="primary"
                htmlType="submit"
                className="form-submit-button"
                style={{ marginTop: '10px', fontWeight: 'bold', fontSize: '18px' }}
              >
                Gi yu cu
              </Button>
            )}
          </div>
        </Form.Item>
        
      </Form>
    </div>
  );
};

export default GplxReissueForm;
